body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #174955;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

#app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 2rem;
}
@media (min-width: 1024px) {
  #app {
    padding: 0;
  }
}
#app h1 {
  color: #FFFFFF;
  font-size: 3rem;
}

.login {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #365A60;
}
@media (min-width: 768px) {
  .login {
    width: 90%;
  }
}
@media (min-width: 1024px) {
  .login {
    width: 40%;
    height: auto;
  }
}

.login-cont {
  padding: 1rem;
  width: 100%;
  color: #FFFFFF;
}
.login-cont h2 {
  text-align: start;
  font-size: 2.6rem;
  margin: 0;
  max-width: 100%;
  margin: auto;
  font-weight: 200;
}
@media (min-width: 768px) {
  .login-cont h2 {
    width: 90%;
  }
}
@media (min-width: 1024px) {
  .login-cont h2 {
    width: 60%;
  }
}
.login-cont .login-form {
  max-width: 100%;
  margin: auto;
  display: flex;
  margin-bottom: 20px;
  flex-direction: column;
}
@media (min-width: 768px) {
  .login-cont .login-form {
    width: 90%;
  }
}
@media (min-width: 1024px) {
  .login-cont .login-form {
    width: 60%;
  }
}
.login-cont .login-form-group {
  margin-bottom: 6px;
}
.login-cont .login-form-group label {
  font-weight: 200;
  margin-bottom: 6px;
}
.login-cont .login-form-group input {
  width: 100%;
  padding: 0.6rem;
  outline: none;
}

.login-form-button {
  border: none;
  padding: 1rem;
  margin: 10px 0 0 0;
  background-color: #E08709;
  color: #FFFFFF;
  transition: all 0.3s;
}
.login-form-button:hover {
  cursor: pointer;
  outline: none;
  transform: scale(1.03);
}

.registro {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #365A60;
}
@media (min-width: 768px) {
  .registro {
    width: 90%;
  }
}
@media (min-width: 1024px) {
  .registro {
    width: 40%;
    height: auto;
  }
}

.registro-cont {
  padding: 1rem;
  width: 100%;
  color: #FFFFFF;
}
.registro-cont h2 {
  text-align: start;
  font-size: 2.6rem;
  margin: 0;
  max-width: 100%;
  margin: auto;
  font-weight: 200;
}
@media (min-width: 768px) {
  .registro-cont h2 {
    width: 90%;
  }
}
@media (min-width: 1024px) {
  .registro-cont h2 {
    width: 60%;
  }
}
.registro-cont .registro-form {
  max-width: 100%;
  margin: auto;
  display: flex;
  margin-bottom: 20px;
  flex-direction: column;
}
@media (min-width: 768px) {
  .registro-cont .registro-form {
    width: 90%;
  }
}
@media (min-width: 1024px) {
  .registro-cont .registro-form {
    width: 60%;
  }
}
.registro-cont .registro-form-group {
  margin-bottom: 6px;
}
.registro-cont .registro-form-group label {
  font-weight: 200;
  margin-bottom: 6px;
}
.registro-cont .registro-form-group input {
  width: 100%;
  padding: 0.6rem;
  outline: none;
}
.registro-cont .info-registro {
  font-size: 1.3rem;
  max-width: 60%;
  margin: auto;
  color: #FFFFFF;
  text-align: center;
}

.registro-form-button {
  border: none;
  padding: 1rem;
  margin: 10px 0 0 0;
  background-color: #E08709;
  color: #FFFFFF;
  transition: all 0.3s;
}
.registro-form-button:hover {
  cursor: pointer;
  outline: none;
  transform: scale(1.03);
}

.alerta {
  text-align: center;
  background-color: #dc3545;
  max-width: 100%;
  margin: auto;
}
.alerta .alerta-text {
  color: #FFFFFF;
  font-size: 1.6rem;
}

#acompanantes {
  margin-bottom: 10px;
}

.controls {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 0.6rem;
  margin-top: 0.6rem;
  background-color: #143f49;
  transition: all 0.3s;
}
@media (min-width: 768px) {
  .controls {
    width: 90%;
  }
}
.controls:hover {
  background-color: #183d46;
}
.controls p {
  color: #FFFFFF;
  margin: 0;
  font-size: 1.6rem;
}
.controls a {
  color: #FFFFFF;
  cursor: pointer;
  transition: all 0.3s;
}
.controls a:hover {
  color: #f09c27;
}
@media (min-width: 1024px) {
  .controls {
    width: 40%;
  }
}

.alerta {
  margin-top: 6px;
  margin-bottom: 6px;
  text-transform: uppercase;
}